.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.centre{
  margin : 0 auto ;
  
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.bgImage{
  background-color: #dadaff ;
}

.headerFont{
  font-family: "Times New Roman", Times, serif, Fantasy;
}

.width_10{
  width: 10% ;;
}

.subHeaderFont{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.marginLeft_12{
  margin-left: 12% ;
}

.card_image{
  width: 200px  !important;
  height: 200px !important ;
}

.blackHexa{
  background-image: url("./assests/images/blackHexa.jpg");
}

.blueTri{
  background-image: url("./assests/images/BlueTriangle.jpg") !important;
}

.darkBlue{
  background-image: url("./assests/images/DarkBlue.jpg") !important;
}

.borderBlac{
  border: 2px solid white  !important  ;
}


.nav-item :hover{
  color: rgb(101, 101, 226)  !important;
}

.selected{
  color: rgb(30, 0, 163)  !important;
}

.cursorPointer{
  cursor: pointer !important;
}

.titlePage { color: #b48608; font-family: 'Droid serif', serif; font-size: 36px; font-weight: 400; font-style: italic; line-height: 44px; margin: 0 0 12px; text-align: center; }


.bgWhite{
  background-color: white !important;
}

.marLeft{
  margin-left: 0 auto;
}

.marRight{
  margin-right: 0 auto;
}

.loader-container{
  position: fixed;
  top: 0 ;
  left : 0 ;
  width: 100%;
  height: 100%;
  background-color: #F8F8F8AD;
}

.loader{
  left: 50%;
  top : 30% ;
  z-index: 1000;
  position: absolute ;
}

.bg_webDev{
  background-image: url("./assests/images/webDev.jpg");
}

.github{
  background-image: url("./assests/images/github.png") !important;
}

.linkedIn{
  background-image: url("./assests/images/linkedin.jpg") !important;
}

.freelancer{
  background-image: url("./assests/images/freeLancer.jpg") !important;
}

.map{
  width: 100% ;
  height : 100% ;
  position: relative ;
}


